.home-parent {
    display: flex;
    flex-direction: column;
    position: relative;
    min-height: 100vh;
    background-size: cover;
}

.home-parent-bg1 {
    background-image: url('../assests/shuffle-01.jpg');
}

.home-parent-bg2 {
    background-image: url('../assests/shuffle-02.jpg');
}

.home-parent-bg3 {
    background-image: url('../assests/shuffle-03.jpg');
}

.home-parent::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #00000091;
    top: 0;
    right: 0;
}

/* small screens list of links */
/* small screens list of links */
.query-list {
    position: absolute;
    width: 100%;
    background-color: #000000b8;
    top: 11%;
    z-index: 999;
    display: none;
    height: 290px;
    overflow: hidden;
    transition: .3s ease-in-out;
}

.query-list-close {
    height: 0 !important;
}

.query-list ul {
    list-style: none;
    padding: 1px 0;
    margin: 0;
}

.query-list ul li {
    text-decoration: none;
    padding: 10px 20px;
    cursor: pointer;
    border-bottom: 1px solid transparent;
    transition: 0.4s all;
}

.query-list ul li:hover {

    border-bottom: 1px solid #19c8fa;
}

.query-list ul li:hover a {

    color: #19c8fa;
}

.query-list ul li a {
    text-decoration: none;
    font-size: 20px;
    color: #e9e9e9;
}



