/* text */
/* text */

.landing-container {
    flex: 1;
    display: flex;
    align-items: center;
}

.landing-text {
    width: 50%;
    background-color: rgba(15, 116, 143, 0.6);
    color: white;
    padding: 5px 70px;
    z-index: 888;
    display: flex;
    flex-direction: column;
    align-items: start;
}

.landing-text h2 {
    font-size: 30px;
}

.landing-text p {
    line-height: 1.8;
}

