/*  */

/* arrows */
/* arrows */

.right-arrow {
    position: absolute;
    width: 30px;
    height: 30px;
    z-index: 999;
    transform: rotate(45deg);
    top: 50%;
    right: 2%;
    border-right: 3px solid white;
    border-top: 3px solid white;
    cursor: pointer;
    transition: 0.4s ease-in-out;
}

.left-arrow {
    position: absolute;
    width: 30px;
    height: 30px;
    z-index: 999;
    transform: rotate(45deg);
    top: 50%;
    left: 2%;
    border-left: 3px solid white;
    border-bottom: 3px solid white;
    cursor: pointer;
    transition: 0.4s ease-in-out;
}

.left-arrow:hover,
.right-arrow:hover {
    transform: rotate(45deg) scale(1.3);
}

/*  */
/*  */
/* Bullets */

.bullets {
    position: absolute;
    display: flex;
    z-index: 999;
    top: 90%;
    right: 48%;
}

.bullets span {
    width: 20px;
    height: 20px;

    border-radius: 50%;
    margin-right: 5px;
    border: 1px solid #19c8fa;
}

.active {
    background-color: #19c8fa;
}

/* Bullets */

@media (max-width: 768px) {
    .query-list {
        display: block;
        height: 290px;
    }

    .icon-list {
        display: flex;
    }

    .landing-text {
        width: 100%;
    }

    .left-arrow,
    .right-arrow {
        width: 15px;
        height: 15px;
    }
}